a, h1, h2, h3, h4, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  display: block;
}

address {
  font-style: inherit;
}

button {
  cursor: pointer;
  background-color: inherit;
  box-shadow: none;
  border: none;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

.is-hidden {
  display: none;
}

.container {
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

body {
  height: 100vh;
  background-color: var(--main--background-color);
  color: var(--main--text-color);
  font-family: Manrope;
}

