.card {
  min-height: 469px;
  margin: 28px 0;
}

.card:first-child {
  margin-top: 0;
}

.card:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 320px) {
  .card {
    width: 288px;
    height: 640px;
  }
}

@media screen and (min-width: 768px) {
  .card {
    width: 353px;
    height: 675px;
    margin: 0;
  }
}

@media (min-width: 1280px) {
  .card {
    width: 395px;
    margin: 0;
  }
}

.bottom-container {
  justify-content: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .render-container {
    flex-flow: row-reverse wrap;
    gap: 30px;
    display: flex;
  }
}

@media (min-width: 1280px) {
  .render-container {
    gap: 31px;
  }
}

.header {
  border-bottom: 1px solid var(--header-border);
}

.header__container {
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__container {
    align-items: baseline;
  }
}

.header.is-opened .header__container {
  height: 100%;
  width: 100%;
  background-color: var(--main--background-color);
  z-index: 9999;
  flex-direction: column;
  align-items: baseline;
  display: flex;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

.header.is-opened .logo {
  width: max-content;
}

.header.is-opened .header__mobile-menu-container {
  width: 100%;
  flex-direction: column;
  flex-grow: 1;
  order: 2;
  justify-content: space-between;
  padding-bottom: 16px;
  display: flex;
}

.header.is-opened .navigation-wrapper {
  min-width: 100%;
  display: flex;
}

.header.is-opened .navigation {
  width: 100%;
}

.header.is-opened .navigation__list {
  max-width: 100%;
}

.header.is-opened .navigation__item {
  width: 100%;
}

.header.is-opened .navigation__link {
  color: var(--main--border-color);
  padding: 11px 0;
  font-weight: 500;
}

.header.is-opened .navigation__link.navigation__item--current {
  background-color: var(--second-additional--text-color);
  color: #f4f4f4;
}

.header.is-opened .header__form-wrap {
  position: absolute;
  top: 27px;
  right: 30px;
}

.header.is-opened .theme-btn {
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  display: flex;
}

.header.is-opened .mobile-buttons-wrapper {
  position: absolute;
  top: 30px;
  right: 20px;
}

.header.is-opened .mobile-buttons-wrapper .header__search-form-opening-btn {
  display: none;
}

.header.is-opened .auth-btn__sign-in {
  width: max-content;
  margin: 0;
  display: flex;
}

.header.is-opened .auth-btn__sign-in.is-hidden {
  display: none;
}

.header.is-opened .auth-btn__sign-out {
  width: max-content;
  margin: 0;
  display: flex;
}

.header.is-opened .auth-btn__sign-out.is-hidden {
  display: none;
}

.overflow-hidden {
  overflow: hidden;
}

.no-scroll {
  width: 100%;
  height: 100%;
  position: fixed;
  overflow: hidden;
}

.is-hidden {
  display: none;
}

