.modal-auth.is-hidden {
  display: none;
}

.modal-auth {
  width: 100%;
  height: 100%;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.modal-auth__form {
  width: 400px;
  background-color: var(--main--background-color);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 0 10px #0000004d;
}

.modal-auth__title {
  text-align: center;
  margin: 0;
  font-size: 20px;
  font-weight: bold;
}

.modal-auth__input {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.modal-auth__wrap-password {
  margin: 20px 0;
  position: relative;
}

.modal-auth__label {
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: bold;
  display: block;
}

.modal-auth__input {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.modal-auth__button-show-password {
  width: 25px;
  height: 25px;
  cursor: pointer;
  opacity: .3;
  background: url("eye-icon.1aaee742.png") center / cover no-repeat;
  border: none;
  position: absolute;
  top: 45%;
  right: 10px;
}

.modal-auth__button-show-password:hover {
  animation: button-pulse .5s ease-in-out infinite;
}

@keyframes button-pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

.modal-auth__buttons-wrap {
  justify-content: space-around;
  display: flex;
}

.modal-auth__button-register, .modal-auth__button-confirm {
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.modal-auth__button-register:hover, .modal-auth__button-confirm:hover {
  background-color: #3e8e41;
}

.modal-auth__error {
  color: red;
  margin-top: 10px;
}

.modal__close-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  opacity: .6;
  background: none;
  border: none;
  position: relative;
  bottom: 70px;
  left: 340px;
}

.modal__close-btn:before, .modal__close-btn:after {
  content: "";
  width: 2px;
  height: 24px;
  background-color: #333;
  position: absolute;
}

.modal__close-btn:before {
  transform: rotate(45deg);
}

.modal__close-btn:after {
  transform: rotate(-45deg);
}

.modal__close-btn:hover:before, .modal__close-btn:hover:after {
  background-color: red;
}

.theme-btn {
  justify-content: flex-end;
  align-items: center;
  margin-left: auto;
  display: none;
}

@media screen and (min-width: 768px) {
  .theme-btn {
    display: flex;
  }
}

.theme-btn__text {
  display: none;
}

@media (min-width: 1280px) {
  .theme-btn__text {
    display: block;
  }
}

@media (min-width: 1280px) {
  .theme-btn__icon {
    display: none;
  }
}

.checkbox {
  opacity: 0;
  position: absolute;
}

.label {
  box-sizing: border-box;
  cursor: pointer;
  height: 20px;
  width: 40px;
  border: .604701px solid #4b48db;
  border-radius: 20px;
  justify-content: space-between;
  align-items: center;
  margin: 0 12px;
  display: flex;
  position: relative;
}

.label .ball {
  height: 16px;
  width: 16px;
  background-color: #4b48db;
  border-radius: 50%;
  transition: transform .2s linear;
  position: absolute;
  left: 1px;
}

.checkbox:checked + .label .ball {
  background-color: #fff;
  transform: translateX(20px);
}

.checkbox:checked + .label {
  background: #4b48db;
  border: 1px solid #fff;
  border-radius: 20px;
}

.top-container {
  display: flex;
}

.days li .inactive {
  color: var(--first-additional--text-color);
}

.calendar-input.isActive::placeholder {
  color: var(--additional-accent--text-color);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.calendar-input.isActive {
  background-color: var(--accent--text-color);
  color: var(--additional-accent--text-color);
  border-color: var(--accent--text-color);
  border: 1px solid #4440f7;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.calendar-wrapper.is-hidden-wrapper {
  pointer-events: none;
}

.calendar-wrapper.is-hidden-wrapper > .calendar-wrapper__container {
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transform: translateY(-104%);
  box-shadow: 0 10px 60px #0000;
}

.calendar-form__text {
  display: none;
}

@media (min-width: 1280px) {
  .calendar-form__text {
    color: var(--main--text-color);
    font-family: Manrope;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 1.38;
    display: inline;
  }
}

.calendar-form {
  margin-left: 40px;
  display: flex;
  position: relative;
}

.calendar-input {
  width: 151px;
  height: 31px;
  border: 1px solid var(--main--border-color);
  text-align: center;
  letter-spacing: .02em;
  color: var(--main--text-color);
  cursor: pointer;
  background-color: #0000;
  border-radius: 20px;
  outline: none;
  justify-content: space-between;
  align-items: center;
  gap: 13px;
  padding: 5px 12px;
  font-family: Manrope;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.75;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .calendar-input {
    width: 168px;
    height: 31px;
    gap: 18px;
    font-size: 14px;
    line-height: 1.5;
  }
}

.calendar__button-calendar {
  width: 17px;
  height: 17px;
  fill: var(--accent--text-color);
  cursor: pointer;
  margin-left: 12px;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
}

.calendar__button-calendar.switchedColor {
  z-index: 1;
  fill: var(--additional-accent--text-color);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.input-form {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.calendar__button-arrow {
  fill: var(--calendar-button-arrow--svg-color);
  cursor: pointer;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  left: 130px;
}

@media screen and (min-width: 768px) {
  .calendar__button-arrow {
    margin-left: 12px;
  }
}

.calendar__button-arrow-icon {
  cursor: pointer;
}

.calendar__button-arrow.switched {
  fill: var(--additional-accent--text-color);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transform: rotateX(180deg);
}

.calendar__button-back {
  fill: var(--accent--text-color);
}

.calendar-wrapper {
  z-index: 2;
  width: 250px;
  height: 350px;
  pointer-events: auto;
  display: block;
  position: absolute;
  right: 0;
  overflow: hidden;
  transform: translateY(40px);
}

.calendar-wrapper__container {
  cursor: default;
  z-index: 2;
  width: 250px;
  height: 250px;
  background: var(--weather--text-color);
  border-radius: 20px;
  margin-left: 0;
  padding: 16px 16px 20px;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  right: 0;
  overflow: hidden;
  box-shadow: 0 10px 60px #0000001a;
}

.calendar-switcher {
  height: 44px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2px;
  padding: 7px 0;
  display: flex;
}

.current-date {
  letter-spacing: -.408px;
  font-feature-settings: "case" on;
  color: var(--calendar--text-color);
  font-family: Manrope;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.38;
}

.calendar-icons {
  color: var(--accent--text-color);
  display: flex;
}

.calendar-icons span {
  width: 38px;
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  fill: var(--accent--text-color);
  border-radius: 50%;
  margin: 0 1px;
  font-size: 1.9rem;
  line-height: 38px;
  display: flex;
}

.calendar-icons span:last-child {
  margin-right: -10px;
}

.calendar-icons span:hover {
  background: var(--weather--text-color);
}

.calendar ul {
  text-align: center;
  flex-wrap: wrap;
  list-style: none;
  display: flex;
}

.calendar li {
  width: 14.2857%;
  letter-spacing: -.078px;
  color: #3c3c434d;
  margin-bottom: 14px;
  font-family: Manrope;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
}

.calendar .days li {
  letter-spacing: .38px;
  color: var(--calendar--text-color);
  z-index: 1;
  cursor: pointer;
  margin-bottom: 12px;
  font-family: Manrope;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.79;
  position: relative;
}

.calendar .days li:nth-last-child(-n+7) {
  margin-bottom: 0;
}

.days li.active {
  color: var(--weather--text-color);
}

.days li.inactive {
  color: var(--first-additional--text-color);
}

.days li:before {
  content: "";
  height: 28px;
  width: 28px;
  z-index: -1;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.days li.active:before {
  background: var(--accent--text-color);
}

.days li:not(.active):hover:before {
  background: #f2f2f2;
}

.datepicker-here {
  color: var(--first-additional--text-color);
}

.logo {
  color: var(--main--text-color);
  margin-right: auto;
  padding: 24px 0;
  font-family: Manrope;
  font-size: 24px;
